/* eslint-disable object-shorthand */
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { EventsService } from 'src/app/services/events.service';
import { LocalService } from 'src/app/services/local.service';
import { RestService } from 'src/app/services/rest.service';
import { UtilityService } from 'src/app/services/utility.service';

@Component({
  selector: 'app-subscriptions',
  templateUrl: './subscriptions.page.html',
  styleUrls: ['./subscriptions.page.scss'],
})
export class SubscriptionsPage implements OnInit {
  loggedUser: any;
  segment: any = 'active';

  activeSubscriptions: any = [];
  inactiveSubscriptions: any = [];
  holdSubscriptions: any = [];
  currency: any;
  constructor(
    public localService: LocalService,
    public restService: RestService,
    public utilityService: UtilityService,
    public eventsService: EventsService,
    private router: Router,
    private activatedRoute: ActivatedRoute
  ) {
    this.loggedUser = this.localService.getUser();
    this.currency = this.localService.getSettings().currency_symbol;
    this.utilityService.loadingOpen('Loading...');
    this.restService
      .getData('user/' + this.loggedUser.id + '/subscriptions')
      .subscribe(
        (data) => {
          this.activeSubscriptions = data.active;
          this.inactiveSubscriptions = data.inactive;
          this.holdSubscriptions = data.hold;
          this.utilityService.loadingClose();
        },
        (err) => {
          // handle error
        }
      );
  }

  ngOnInit() {}
  bookingDetails(id) {
    this.router.navigate(['/orderdetails'], {
      queryParams: {
        id: id,
        mode: 'subscription',
      },
    });
  }

  doRefresh(event) {
    this.restService
      .getData('user/' + this.loggedUser.id + '/subscriptions')
      .subscribe(
        (data) => {
          this.activeSubscriptions = data.active;
          this.inactiveSubscriptions = data.inactive;
          this.holdSubscriptions = data.hold;
          event.target.complete();
        },
        (err) => {
          // handle error
          event.target.complete();
        }
      );
  }
}
